import { useEffect, useState } from 'react';
import { UserListApi } from '../../utils/adminApi';
import { Table, Typography } from 'antd';
const { Column } = Table;

const Withdraw = () => {
  const [tableData, setTableData] = useState();

  useEffect(() => {
    UserListApi().then((res) => {
      setTableData(res.data.map((item) =>({...item, _id: item._id.slice(0,7),  createdAt: item.createdAt.replace(/[TZ]/g,' '), updatedAt: item.updatedAt.replace(/[TZ]/g,' ')}) ));
    });
  }, []);

  const edit = () => {};
  const del = () => {};

  return (
    <Table dataSource={tableData} rowKey="_id" >
      <Column title="ID" dataIndex="_id"  />
      <Column title="会员编号" dataIndex="nick"  />
      <Column title="提现金额" dataIndex="blance"  />
      <Column title="手续费" dataIndex="blance"  />
      <Column title="实发金额" dataIndex="blance"  />
      <Column title="提现方式" dataIndex="nick"  />
      <Column title="提现帐号" dataIndex="freeze" render={(_,record) => <img className='w-24 h-24 bg-red-400' />} />
      <Column title="申请时间" dataIndex="createdAt"  />
      <Column title="状态" dataIndex="state"  />
      <Column
        title="操作"
        dataIndex=""
        render={(_, record) => (
					<div className='flex gap-1' >
          <Typography.Link onClick={() => del(record)}>提现处理</Typography.Link>
					</div>
        )}
      />
    </Table>
  );
};

export default Withdraw;
